.name, .quantity {
    font-family: 'IBM Plex Mono' !important; }


.addOrderItem {
    display: grid;
    grid-template-columns: 8fr 1fr;
    padding: 2em 1em;
    input {
      font-family: 'IBM Plex Mono' !important; }

    .MuiFormControl-root {
        padding: 0 1rem !important;
        label {
            padding-left: 1em; } }

    .fullNameInput {
      margin-top: 1rem;
      input {
        font-family: sans-serif; } }

    .quantityInput {
      margin-left: 1em; }

    .pushOrderItemButton {
        border: none !important;
        display: flex;
        align-self: end; } }


.Unit {
    position: relative;
    padding: 1em;
    padding-left: 2em;
    &:hover {
        .orderItemActions {
            opacity: 1; } }
    .orderItemActions {
        margin-top: .5em;
        opacity: 0;
        color: var(--text2);
        bottom: 0;
        left: 0;
        font-size: .7rem;
        font-family: none;
        text-decoration: underline;
        transition: opacity .5s ease !important;
        a {
            margin-right: 1em;
            &:hover {
                color: var(--text1); } } } }


.firstRow {
    display: grid;
    grid-template-columns: 5fr .8fr 1fr;

    .name, .quantity {
        font-size: 1.3rem; }
    .quantity {
        color: var(--accent);
        font-weight: 600; } }

.fullName {
    color: var(--text2) !important;
    padding-top: 5px;
    font-size: .9rem; }
